.nav-buttons {
	display: flex;
	justify-content: space-around;
	align-items: center;
	background-color: var(--dark-indigo);
	border-top: 2px solid black;
	height: 7rem;
	width: 100%;
	z-index: 1000;
	position: relative;
}

.home-btn,
.back-btn {
	width: 10rem;
	height: 6rem;
	filter: invert(100%);
	cursor: pointer;
	padding: 1rem 3rem;
}